.tui-pagination .tui-ico-ellip,
.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-last {
  width: 16px;
  height: 16px;
  fill: var(--accent-color);
  transition: all 250ms ease-in-out;
}

.section-pagination {
  margin-top: 40px;
  margin-bottom: 40px;

  @media screen and (min-width: $tablet) {
    margin-top: 60px;
    margin-bottom: 60px;
  }
}

.tui-pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  //   height: 40px;
  //   line-height: normal;
  text-align: center;
  //   font-size: 0;
}

// .tui-pagination
.tui-page-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;

  font-family: 'Roboto';
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  background: var(--main-color);
  border-radius: 5px;
  border: 0;
  color: var(--second-color);
  &:hover {
    background-color: var(--accent-color);
    color: var(--main-color);
  }
  transition: all 250ms ease-in-out;
}

// .tui-page-btn:hover {
//   background-color: var(--accent-color);
//   color: var(--main-color);
// }

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: var(--main-color);
  background: var(--accent-color);
  cursor: default;
}

.tui-prev,
.tui-next {
  background-color: var(--footer-bg-color);
  //   transform: matrix(-1, 0, 0, 1, 0, 0);
}

.tui-first,
.tui-last {
  display: none;
}

.tui-pagination .tui-is-disabled:hover {
  background-color: var(--footer-bg-color);
  cursor: default;
}

.tui-prev-is-ellip,
.tui-next-is-ellip {
  @media screen and (max-width: $tablet) {
    display: none;
  }
}

